import "./arts.scss";
import { useState } from 'react';

export default function Arts() {
    const [currentSlide, setCurrentSlide] = useState(0);
    const data = [
        
        {
            id:1,
            title: "image1",
            img:"https://www.youtube.com/embed/3yjkqZFuYUY",
        },
        {
            id:2,
            title: "image2",
            img: "https://www.youtube.com/embed/w-EoAXhKOLk",
        },
        {
            id:3,
            title: "image3",
            img: "https://www.youtube.com/embed/3yjkqZFuYUY",
        },
        {
            id:4,
            title: "image4",
            img: "https://www.youtube.com/embed/eSSq6a_3CiY",
        },
        {
            id:5,
            title: "image5",
            img: "https://www.youtube.com/embed/fD-tPhAgwJA",
        },


    ];  
    const handleClick =  (way)=>{
        way === "left" ? setCurrentSlide(currentSlide > 0 ? currentSlide-1 : currentSlide+data.length-1)  :
        setCurrentSlide(currentSlide<data.length -1 ? currentSlide+1 : 0)
    }

    return (
        <div className="arts" id="arts">
            <div className="slider" style = {{transform: `translateX(-${currentSlide * 100}vw)` }}>
        {data.map(d=>(  
             <div className="container">
                    <div className="item">
                        <div className="anh">
                        <iframe width="100%" height="100%" src={d.img} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>                      
                        </div>
                    </div>
                </div> 
              ))}   
            </div>
        
            <img src="assets/Arrowfixed.png" className="arrow left" alt="" onClick={()=>handleClick("left")}/>
            <img src="assets/Arrowfixed.png" className="arrow right" alt="" onClick={()=>handleClick()}/>
        </div>

    )
}
